	import common from '../../common/common.js'
	import util from '../../common/util.js'
	
	export default {
		data() {
			return {
				baseUrl:common.baseUrl,
				userid:'',
				userInfo:[],
				order:[],
				orderno:'',				
				maishou:[],			//买手信息
				coupon_disamount:0,	//优惠券减免
				orderQR:'',			//订单二维码
				showOrderQR:false,	//是否显示核销码
				status:'待支付',
				ntime:'',
				
				info:{
					delta:2,
				},
				
				titleHeight:0,
				statusBarHeight:0,
				naviBarHeight:0,
				superTitleHeight:0,
			};
		},
		onLoad(e) {
			var that = this
			var userid = uni.getStorageSync('userid');
			var userInfo = uni.getStorageSync('userInfo');
			console.log(userInfo);
			that.userid = userid;
			that.userInfo = userInfo;
			var orderno = e.id;
			that.orderno = orderno
			that.getOrderInfo();
			
			var ntime = (Date.parse(ntime) / 1000);//当前时间戳
			that.ntime = ntime
			
			var act = e.act;
			if(act == 'payfee'){
				that.payfee();
			}
			that.titleHeight = common.titleHeight;
			that.statusBarHeight = common.statusBarHeight;
			that.naviBarHeight = common.naviBarHeight;
			that.superTitleHeight = common.titleHeight + 50;



			
		},
		onPullDownRefresh() {
			var that = this
			uni.showNavigationBarLoading();
			console.log('下拉刷新中...');	
			var orderno = that.orderno;

			that.getOrderInfo();
			
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();
		},
		onShareAppMessage(res) {
			var that = this
			if (res.from === 'button') {// 来自页面内转发按钮
			  //console.log(res.target)
			}
			var userInfo = uni.getStorageSync('userInfo');
			var goods_id = that.order.goods_id
			var tuanid = that.order.order_tuanid
			var url = '/pages/tuan/tuanview?id='+goods_id+'&tuanid='+tuanid;
			var imageUrl = common.baseUrl + that.order.goods_picture
				
			
			var shareUrl = url
			console.log(shareUrl);
			return {
			  title: that.order.goods_title,
			  path: shareUrl,
			  imageUrl:imageUrl
			}
		},
		methods:{
			//导航
			daohang(){
				var that = this
				var lat = parseFloat(that.order.shop_lat)
				var lng = parseFloat(that.order.shop_lng)
				uni.openLocation({
					latitude: lat,
					longitude: lng,
					success: function () {
						console.log('success');
					}
				});
			},
			toggleOrderQR(){
				var that = this
				var showOrderQR = that.showOrderQR
				that.showOrderQR = !showOrderQR
			},
			//核销码
			orderQRCode(){
				var that = this
				var orderno = that.orderno
				uni.request({
					url: common.baseUrl + 'sharepic/getOrderQR',
					method: 'GET',
					data: {
						orderno:orderno
					},
					success: res => {
						console.log("orderQR",res);
						that.orderQR = res.data.orderQR
						that.toggleOrderQR();
					},
					fail: () => {},
					complete: () => {}
				});
				
			},
			//授权并支付
			authpay(){
				var that = this
				uni.login({
				  provider: 'weixin',
				  success: function (loginRes) {
					console.log(loginRes);
					var code = loginRes.code;
					// 获取用户信息
					uni.getUserInfo({
					  provider: 'weixin',
					  success: function (infoRes) {
						
						var rawData = infoRes.rawData;
						var signature = infoRes.signature;
						var iv =  infoRes.iv;
						var encryptedData = infoRes.encryptedData;
						uni.request({
							url: common.apiUrl+'login/updateUserInfo',
							method: 'GET',
							data: {
								userid:that.userid,
								code:code,
								rawData:rawData,
								signature:signature,
								iv:iv,
							},
							success: res => {
								console.log(res);
								if(res.data.code == 0){
									var userInfo  = res.data.userInfo;
									uni.setStorageSync('userInfo',userInfo);
									that.userInfo = userInfo;
									that.payfee();
								}else{
									uni.showToast({
										title: res.msg,
										duration: 1000,
										icon :'none'
									})
								}
							},
							fail: () => {},
							complete: () => {}
						});
						
					  }
					});
				  }
				});
			},
			
			//支付订单
			payfee(){
				var that = this
				var userid = that.userid
				var orderno = that.orderno
				console.log(userid);
				console.log('orderno',orderno)
// 				if(that.all_amount <= that.coupon_disamount){
// 					return false
// 				}
				uni.request({
					url: common.apiUrl+'pay/payfee',
					method: 'GET',
					data: {
						userid:userid,
						order_no:orderno,
						otype:1
					},
					success: res => {
						console.log(res)
						var timeStamp = res.data.timeStamp
						var nonceStr = res.data.nonceStr;
						var vpackage = res.data.package;
						var paySign = res.data.paySign;
						//console.log("nonceStr",nonceStr);
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: timeStamp,
							nonceStr: nonceStr,
							package: vpackage,
							signType: 'MD5',
							paySign: paySign,
							success: function (res) {
								uni.showModal({
									title: '提示',
									content: '支付成功',
									showCancel: false,
									cancelText: '',
									confirmText: '确定',
									success: res => {
										setTimeout(function () {
											// uni.reLaunch({
											//   url: '/pages/orders/tuaninfo?id='+that.orderno
											// })
											that.getOrderInfo();
										}, 1000) //延迟时间
									},
									fail: () => {},
									complete: () => {}
								});
								// uni.showToast({
								// 	title: '支付成功',
								// 	icon: 'success',
								// 	duration: 2000,
								// 	success(ress) {
								// 	  setTimeout(function () {
								// 		uni.reLaunch({
								// 		  url: '/pages/orders/orders'
								// 		})
								// 	  }, 1000) //延迟时间
								// 	}
								//   });
							},
							fail: function (err) {
								console.log('fail:' + JSON.stringify(err));
							}
						});
					},
					fail: () => {},
					complete: () => {}
				});
				//console.log(that.userInfo);
				//console.log('开始支付');
			},
			
			//获取订单列表
			getOrderInfo(){
				var that = this
				var orderno = that.orderno;
				console.log("orderno",orderno);
				uni.request({
					url: common.apiUrl+'orders/getSuperOrderInfo',
					method: 'GET',
					data: {
						orderno:orderno
					},
					success: res => {
						console.log("res",res);
						that.order = res.data.order
						that.addtime = res.data.order.addtime
						that.status = res.data.status
						console.log(that.addtime);
					},
					fail: () => {},
					complete: () => {}
				});
			},
			
			//取消订单倒计时
			cancelOrderCountDown(){
				var that = this
				var addtime = that.addtime
				var mdate = new Date();
				var now = mdate.getTime();
				now = now/1000;
				console.log(now);
				if(now-addtime < 60 && that.pay_status >= 0){
					var ltime = 60-(now-addtime);
					var ss = util.formatTime(ltime);
					that.counttime = ss
					
					//setInterval(cancelOrderCountDown,1000);
					setTimeout(function() {  
						that.cancelOrderCountDown()  
					},  
					1000)
					that.isneworder = 1;
				}else{
					clearTimeout(function(){
						that.cancelOrderCountDown()
					})
					that.isneworder = 0;
				}
				
			},
			
			//联系骑手
			contactRider(){
				var that = this
				var psuser = that.psuser
				if(psuser != ''){
					uni.makePhoneCall({
						phoneNumber:psuser.mem_mobile
					})
				}else{
					uni.showToast({
						title:'获取骑手信息失败',
						duration:1000
					})
				}
			},
			
			//联系买手
			contactMaishou(){
				var that = this
				var maishou = that.maishou
				if(maishou != ''){
					uni.makePhoneCall({
						phoneNumber:maishou.mem_mobile
					})
				}else{
					uni.showToast({
						title:'获取买手信息失败',
						duration:1000
					})
				}
			},
			//链接
			jumpUrl(e){
				var that = this
				var url = e.currentTarget.dataset.url;
				uni.navigateTo({
					url:url
				})
			},
			
			

		}
	}
